import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import * as Rx from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-add-enquiry',
  templateUrl: './add-enquiry.component.html',
  styleUrls: ['./add-enquiry.component.css']
})
export class AddEnquiryComponent implements OnInit {
  enquiryForm: FormGroup
  enqNo: any = "";
  btnLabel: any = "";
  data: any;
  constructor(
    private fb: FormBuilder,
    private http: HttpClient,
    private activateRoute: ActivatedRoute,
    private router: Router
  ) { 
    this.activateRoute.params.subscribe(enquiryNo => {
      this.enqNo = enquiryNo && enquiryNo.enqNo || ''
    })
    this.btnLabel = "Submit"
    if(this.enqNo !== '') {
      this.getEnquiry();
    }
  }

  ngOnInit(): void {
    this.enquiryForm = this.fb.group({
      enquiryNo: ['', [Validators.required]],
      file: ['', [Validators.required]],
      date: ['', [Validators.required]],
      suspectName: ['', [Validators.required]],
      enqOfficerName: ['', [Validators.required]],
      status: ['pending'],
    })
  }

  getEnquiry() {
    this.http.get(`http://localhost:2023/api/svu/v1/getenqbyid?enqNo=${this.enqNo}`).subscribe(res => {
    this.data = res && res['data'];
    this.enquiryForm.get('enquiryNo').patchValue(this.data && this.data.enquiryNo)
    let caseDate =  res && res['data'] && res['data'].date;
    let dateObj = {
      year: new Date(caseDate).getFullYear(),
      month: new Date(caseDate).getMonth(),
      day: new Date(caseDate).getDate()
    }
    console.log(dateObj);
    
    this.enquiryForm.get('date').patchValue(dateObj)
    this.enquiryForm.get('suspectName').patchValue(this.data && this.data.suspectName)
    this.enquiryForm.get('enqOfficerName').patchValue(this.data && this.data.enqOfficerName)
    this.enquiryForm.get('status').patchValue(this.data && this.data.status)
    })
  }
  onFileUpload(event) {
    this.enquiryForm.get('file').patchValue(event.target.files[0])
   this.studentBulkUpload(this.enquiryForm.get('file').value).subscribe(res => {
    console.log(res);
  })
  }

  studentBulkUpload(file): Rx.Observable<any> {
    const formData = new FormData();
    formData.append('file', file);
    return this.http.post(`http://localhost:2023/api/svu/v1/saveFiles?caseNo=${this.enquiryForm.get('enquiryNo').value}`, formData)
  }

  submit() {
    let getDate = this.enquiryForm.get('date').value
      let month = getDate.month;
      let year = getDate.year;
      let day = getDate.day
      const isoDate = month.toString() + "/" + day.toString()  + "/" + year.toString();
      this.enquiryForm.get('date').setValue(new Date(isoDate).toISOString())
    const payLoad = {
      date: this.enquiryForm.get('date').value,
      enqOfficerName: this.enquiryForm.get('enqOfficerName').value,
      enquiryNo: this.enquiryForm.get('enquiryNo').value,
      status: this.enquiryForm.get('status').value,
      suspectName: this.enquiryForm.get('suspectName').value,
    }    
    if(this.enqNo === "") {
      return this.http.post('http://localhost:2023/api/svu/v1/updateEnquiry', this.enquiryForm.getRawValue()).subscribe(res => {
        this.router.navigate(['tables'])
      })
    } else {
      return this.http.put(`http://localhost:2023/api/svu/v1/updateEnquiry?enqNo=${this.enqNo}`, this.enquiryForm.getRawValue()).subscribe(res => {
        this.router.navigate(['tables'])
        
      })
    }
   


  }

  previewFile() {
    window.open(`http://localhost:2023/api/svu/v1/downloadFile?caseNo=${this.enqNo}`, 'blank')

  }
}
